<template>
<div>
        <p class="db">abc</p>
        <p class="qiu" @click="gai()"></p>
</div>
</template>

<script>
import $ from 'jquery'
import bus from '../plugins/bus'
exportdefault{
data(){
return{
            flag:true
};
},
created(){

},
mounted(){

},
methods:{
        gai(){
            this.flag=!this.flag
            this.$emit('gb',this.flag)
            if(this.flag){
                // this.flag=false
                $('.qiu').css('right','23px')
                $('.qiu').css('transition','0.3s')
                $('.db').css('background','#4CD964')
            }else{
                // this.flag=true
                $('.qiu').css('right','60px')
                $('.qiu').css('transition','0.3s')
                $('.db').css('background','#CCCCCC')
            }
        }
}
};
</script>

<stylescoped lang='scss'>
.db{
    width: 0.6rem;
    height: 0.25rem;
    line-height: 0.25rem;
    text-indent: 0.1rem;
    border-radius: 0.5rem;
    position: absolute;
    top: 1.27rem;
    right: 0.31rem;
    background: #CCCCCC;
    color: white;
}
.qiu{
    width: 0.33rem;
    height: 0.33rem;
    border-radius: 0.5rem;
    position: absolute;
    top: 1.23rem;
    right: 0.6rem;
    background: #eee;
}
</style>
